<template>
  <div>
    <el-tree
      :data="data"
      :props="defaultProps"
      @node-click="handleNodeClick"
      updateKeyChildren="updateKey"
    ></el-tree>
  </div>
</template>

<script>
export default {
  data() {
    return {
      defaultProps: {
        children: "children",
        label: "name"
      },
      data: [
      ]
    };
  },
  mounted() {
    this.$post("structure/getstructure").then(res => {
      // res.roles.forEach(item => {
      //   item.text = item.role_name;
      //   item.value = item.role_id;
      // });
      // res.faculties.forEach(item => {
      //   item.text = item.faculty_name;
      //   item.value = item.faculty_id;
      // });
      this.data = res.structure;
    });
  },
  methods: {
    handleNodeClick() {},
    updateKey(key, data) {
      console.log(key, data);
    }
  }
};
</script>

